import React from 'react';
import CameraPhoto, { FACING_MODES } from 'jslib-html5-camera-photo';
import IconButton from '@material-ui/core/IconButton';
import AddAPhotoIcon from '@material-ui/icons/AddAPhoto';
import { Grid } from '@material-ui/core';
import Paper from '@material-ui/core/Paper'
import axios from 'axios';
import { LinearProgress } from '@material-ui/core';

class Camera extends React.Component {

  constructor(props, context) {
    super(props, context);
    this.cameraPhoto = null;
    this.paper = {
      maxWidth: 936,
      margin: 'auto',
      overflow: 'hidden',
    }
    this.videoRef = React.createRef();
    this.state = {
      dataUri: 'diagnosis.png',
      isCamStart: false,
      predictions: [],
      progress: false

    }

  }

  componentDidMount() {
    this.cameraPhoto = new CameraPhoto(this.videoRef.current);
  }

  startCamera(idealFacingMode, idealResolution) {
    console.log('start Camera');
    this.cameraPhoto.startCamera(idealFacingMode, idealResolution)
      .then(() => {
        console.log('camera is started !');
      })
      .catch((error) => {
        console.error('Camera not started!', error);
      });
  }

  startCameraMaxResolution(idealFacingMode) {
    this.cameraPhoto.startCameraMaxResolution(idealFacingMode)
      .then(() => {
        console.log('camera is started !');
      })
      .catch((error) => {
        console.error('Camera not started!', error);
      });
  }

  sleep(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
  }

  map(predictions) {
    let p = []
    for (var key in predictions) {
      p.push(key + ": " + predictions[key]);
    }
    return p;
  }

  async setPrediction(jobId) {
    //todo: try scoket communication... real time u know ;)
    let host = "http://127.0.0.1:8000";
    host = 'https://diagnosis-test.herokuapp.com';
    let self = this;
    await this.sleep(5000); //todo: sleep time in submit resp. depends on server load
    await axios.get(host + "/status/" + jobId + "/").then(function (response) {
      let predictions = self.map(response.data['prediction']);
      self.setState(ps => ({ ...ps, predictions }));
      self.setState(ps => ({ ...ps, progress:false }));
    });
  }

  takePhoto() {
    let progress = true;
    let self = this;
    let dataUri = this.cameraPhoto.getDataUri({ sizeFactor: 1 });
    let host = "http://127.0.0.1:8000";
    host = 'https://diagnosis-test.herokuapp.com';
    self.setState(ps => ({ ...ps, progress }));
    axios.post(host + /submit/, { 'img': dataUri.substr(22) })
      .then(function (response) { self.setPrediction(response.data['jobId']) });
  }

  stopCamera() {
    console.log('stop Camera');
    this.cameraPhoto.stopCamera()
      .then(() => {
        console.log('Camera stoped!');
      })
      .catch((error) => {
        console.log('No camera to stop!:', error);
      });
  }

  onPhotoClick() {
    if (!this.state.isCamStart) {
      let isCamStart = !this.state.isCamStart;
      this.setState(ps => ({ ...ps, isCamStart }),
        () => {
          //console.log(this.state.isCamStart)
        }
      );
      this.startCamera(FACING_MODES.ENVIRONMENT, { width: 320, height: 320 });

    }
    else {
      let isCamStart = !this.state.isCamStart;
      this.setState(ps => ({ ...ps, isCamStart }),
        () => {
          console.log(this.state.isCamStart)
        }
      );
      this.takePhoto();
      this.stopCamera();
    }
  }

  onAnalyse() {
  }

  render() {
    return (
      <Paper className={this.paper}>
        <Grid container spacing={2} alignItems="center">
          <Grid item>
            <video style={{ display: this.state.isCamStart ? '' : 'none' }} ref={this.videoRef} autoPlay={true} />
            <img
              alt=''
              src={this.state.dataUri}
              width='320px'
              height='235px'
              style={{ display: this.state.isCamStart ? 'none' : '' }}
            />
          </Grid>
        </Grid>
        <Grid container spacing={2} alignItems="center">
          <Grid item>
            <IconButton onClick={() => { this.onPhotoClick(); }}>
              <AddAPhotoIcon />
            </IconButton>
          </Grid>
        </Grid>
        {this.state.progress && <LinearProgress m={3} p={3} />}
        <Grid container spacing={2} alignItems="center">
          <Grid item>
            {this.state.predictions.length > 0 && <h2>Predictions</h2>}
            {this.state.predictions.map((i) => <p p={1} m={1}> {i}</p>)}
            
          </Grid>
        </Grid>

      </Paper>
    );
  }
}

export default Camera;